import React, {useEffect} from 'react'
import ReactSelect, {components} from 'react-select'
import FormField from './FormField'
import useStore from '../hooks/useStore'
import TagWithIcon from './TagWithIcon'
import {observer} from 'mobx-react'
import {TagType} from '../domain/Tag'

const MultiValueLabel: React.FC<any> = (props) => {
  return (
      <components.MultiValueLabel {...props}>
        <TagWithIcon name={props.data.tag?.name} type={props.data.tag?.type}/>
      </components.MultiValueLabel>
  )
}

const Option: React.FC<any> = (props) => {
  return (
      <components.Option {...props}>
        <TagWithIcon name={props.data.tag?.name} type={props.data.tag?.type}/>
      </components.Option>
  )
}

export interface ProjectTagsFieldProps {
  projectId?: string
}

const ProjectTagsField: React.FC<ProjectTagsFieldProps> = ({projectId}) => {
  const {tagStore, projectTagStore} = useStore()

  useEffect(() => {
    tagStore.loadAll()
  }, [tagStore])

  useEffect(() => {
    if (projectId) {
      projectTagStore.loadByProject(projectId)
    }
  }, [projectTagStore, projectId])

  const tags = tagStore.tagsForProject

  if (!tags.length) {
    return null
  }

  let defaultValue: null | { value: string, label: string }[] = null

  if (projectId) {
    defaultValue = projectTagStore.getItemsByProject(projectId)
        .filter(projectTag => projectTag.tag?.type !== TagType.POSITION)
        .map(projectTag => ({
          value: projectTag.tagId,
          label: projectTag.tag?.name,
          tag: projectTag.tag
        }))
  }

  return (
      <FormField name="tags" label="Tags" defaultValue={defaultValue}>
        <ReactSelect
            isMulti
            components={{Option, MultiValueLabel}}
            options={tags.map(tag => (
                {
                  value: tag.id,
                  label: tag.name,
                  tag,
                }
            ))}
        />
      </FormField>
  )
}

export default observer(ProjectTagsField)