import React from 'react'
import Tabs from '../ui/Tabs'
import useQueryValue from '../hooks/useQueryValue'
import {useHistory} from 'react-router-dom'
import {slugify} from '../util/string'

interface RouterTabsProps {
  name?: string
}

const RouterTabs: React.FC<RouterTabsProps> = ({name, children}) => {
  const history = useHistory()
  const currentSlug = useQueryValue(name)

  const slugs: string[] = React.Children.map(children, (child, index) => {
    if (!React.isValidElement(child)) {
      return
    }

    return child.props.title ? slugify(child.props.title) : index.toString()
  })

  return (
      <Tabs
          index={currentSlug ? slugs.indexOf(currentSlug) : 0}
          onChange={(index) => history.push(`?${name}=${slugs[index]}`)}
      >
        {children}
      </Tabs>
  )
}

RouterTabs.defaultProps = {
  name: 'tab'
}


export default RouterTabs